
import { defineComponent } from "vue";
import { ref, reactive } from "vue";
import { compareDates } from "@/utils/helper";
import { useStore } from "@/store/store";
export default defineComponent({
  name: "DocumentsFilter",
  async setup() {
    const store = useStore();
    const { methods } = store;
    const error = ref(false);
    const sidebarOpened = ref(false);
    const errorText = ref("");
    const dateFrom = ref("");
    const dateTo = ref("");
    const masks = reactive({
      input: "DD-MM-YYYY",
    });

    // handle Apply filter
    const applyFilter = () => {
      if (dateFrom.value === "" || dateFrom.value === null) {
        error.value = true;
        errorText.value = "Input value can't be null";
      } else if (dateTo.value == "" || dateTo.value === null) {
        error.value = true;
        errorText.value = "Input value can't be null";
      } else if (compareDates(dateFrom, dateTo)) {
        error.value = true;
        errorText.value = "Date To should be bigger than Date From";
      } else {
        error.value = false;
        methods.filterByDate(dateFrom.value, dateTo.value);
        toggleFilter();
      }
    };

    // clear filter
    const clearFilters = () => {
      dateFrom.value = "";
      dateTo.value = "";
      methods.resetFilter();
      toggleFilter();
    };
    // handle filter on mobile view
    const toggleFilter = () => {
      sidebarOpened.value = !sidebarOpened.value;
    };
    return {
      error,
      errorText,
      dateFrom,
      dateTo,
      masks,
      applyFilter,
      clearFilters,
      toggleFilter,
      sidebarOpened,
    };
  },
});
